

.Nav{
    min-height: 100px;
    height: 100px;
    background-color:transparent;
    display: flex;
    align-items: center;
    float:right    
}

.navlist{
   
    list-style: none;
    margin: 40px;
    
    
    flex-grow: 1;
    
    a{
        font-family: Raleway;
        font-weight: bold;
        font-size: 1.5rem;
        display: inline;
        flex-grow: 3;
        text-decoration: none;
        color: black;
        
    }

    a:hover{
        color:#FB4B4E;
        transform: scale(1.20);
    }
}