
@font-face {
  font-family: 'Raleway' ;
  src: local("/nyportfolio/src/assets/Raleway-VariableFont_wght.ttf");
}

@font-face{
  font-family: 'Raleway-italic';
  src:local("/nyportfolio/src/assets/Raleway-Italic-VariableFont_wght.ttf");
  
  

}



.App {
  text-align: center;
  min-height: 100vh;
  background-image: linear-gradient(45deg, #93a5cf 0%, #e4efe9 100%);

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* HERO */

.herocontainer{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70vh;
  min-height: 70vh;
  width: 100vw;
  min-width: 100vw;
}